html {
  background-color: black;
}

body {
  overflow: hidden;
}

.HolyGrail {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  background-color: black;
  color: white;
}

.HolyGrail-header {
  width: 100%;
  position: fixed;
  background: black;
  text-align: center;
  border-bottom: 1px solid white;
  z-index: 1;
}

.HolyGrail-header p strong,
.HolyGrail-header p button {
  margin-left: 20px;
}

.HolyGrail-body {
  display: flex;
  flex: 1;
  margin-top: 160px;
}

.HolyGrail-content {
  flex-shrink: 0;
  flex-basis: 1001px;
  /* all inner element are positioned, so width = 0, need this or specific width = 10001px */
  margin-left: auto;
  /* need flex-grow: 0 to work */
  margin-right: auto;
  /* need flex-grow: 0 to work */
  position: relative;
}

.HolyGrail-content div {
  position: absolute;
  top: 0;
  /* controlable by javascript */
  left: 0;
  /* controlable by javascript */
}

.HolyGrail-content div table {
  border-collapse: collapse;
  padding: 0;
}

.HolyGrail-content div table:nth-child(1n+2) {
  position: absolute;
  top: 0;
  left: 0;
}

.HolyGrail-content div table td {
  width: 10px;
  height: 10px;
  background-color: transparent;
  padding: 0;
}

.HolyGrail-content div table td.player {
  background-color: blue;
}

.HolyGrail-content div table td.enemy {
  background-color: red;
}

.HolyGrail-content div table td.medicine {
  background-color: green;
}

.HolyGrail-content div table td.room {
  background-color: white;
}

.HolyGrail-content div table td.stairs {
  background-color: purple;
}

.HolyGrail-content div table td.black {
  background-color: black;
}

.HolyGrail-footer {
  text-align: center;
  background-color: black;
  padding-top: 10px;
  padding-bottom: 10px;
  border-top: 1px solid white;
  position: relative;
}

/* https://css-tricks.com/pop-from-top-notification/ */
#note {
  position: absolute;
  z-index: 6001;
  top: 0;
  left: 0;
  right: 0;
  background: #fde073;
  text-align: center;
  line-height: 2.5;
  overflow: hidden;
  -webkit-box-shadow: 0 0 5px black;
  -moz-box-shadow: 0 0 5px black;
  box-shadow: 0 0 5px black;
  color: black;
  font: bold;
}

#note {
  -webkit-transform: translateY(-50px);
  -webkit-animation: slideDown 2.5s 1.0s 1 ease forwards;
  -moz-transform: translateY(-50px);
  -moz-animation: slideDown 2.5s 1.0s 1 ease forwards;
}

@-webkit-keyframes slideDown {
  0%, 100% {
    -webkit-transform: translateY(-50px);
  }
  10%, 90% {
    -webkit-transform: translateY(0px);
  }
}

@-moz-keyframes slideDown {
  0%, 100% {
    -moz-transform: translateY(-50px);
  }
  10%, 90% {
    -moz-transform: translateY(0px);
  }
}
